import React from 'react'
import styled from 'styled-components'
import Hash from './Hash'
interface HashButtonProps extends React.HTMLAttributes<HTMLDivElement> {
    color?: string
}

const ButtonContainer = styled.span<HashButtonProps>`
    display: inline-flex;
    cursor: pointer;
    gap: 4px;
    user-select: none;
    .hash-button-text {
        display: inline-flex;
        align-items: center;
        background-color: #fff;
        font-family: var(--font-titles);
        font-size: 1rem;
        text-transform: uppercase;
        padding: 0 8px;
        color: #000;
        align-items: center;
        vertical-align: middle;
    }
`

const HashButton: React.FunctionComponent<HashButtonProps> =
    (props) => {
        return (
            <ButtonContainer {...props}>
                <Hash color={props.color} />
                <span className="hash-button-text">{props.children}</span>
            </ButtonContainer>
        )
    }

export default HashButton