import React from 'react'
import styled from 'styled-components'
import { MOBILE_QUERY } from '../helpers'
import { motion, MotionProps } from 'framer-motion'
export interface ResumeEntryProps {
    date: string
    location: string
    title: string
    employer: {
        name: string,
        webiste: string
    }
    description?: string
}
const ResumeEntryContainer = styled(motion.section)`
    padding-left: 16px;
    max-width: 550px;
    margin: 64px 0;
    border-left: 1px solid #fff;
    font-family: var(--font-titles) !important;

    .date-and-location {
        font-size: .9rem;
        color: rgba(255,255,255, .6);
        margin-bottom: 16px
    }
    .title {
        display: inline-block;
        background-color: var(--highlight-gray);
        padding: 6px 12px;
        font-size: 1.4rem;
    }
    .employer {
        display: block;
        color: #fff;
        margin-bottom: 32px;
        margin-top: 8px;
    }
    .description {
        font-size: 1rem;
        line-height: 150%;
    }

    ${MOBILE_QUERY}{
        max-width: 320px;
        .title {
            font-size: 1.2rem;
        }    
    }
`
const ResumeEntry: React.FC<ResumeEntryProps & MotionProps> = (props) => {
    return (
        <ResumeEntryContainer {...props}>
            <p className="date-and-location">
                {props.date} - {props.location}
            </p>
            <p className="title">
                {props.title}
            </p>
            <a className="employer" href={props.employer.webiste}>@{props.employer.name}</a>

            {props.location && <p className="description">{
                props.description
            }</p>}

        </ResumeEntryContainer>
    )
}

export default ResumeEntry

