import React from 'react'
import Page, { PageIndicator } from '../components/Page'
import Rule from '../components/Rule'
import Container from '../components/Container'
import ProjectThumbnail from '../components/ProjectThumbnail'
import styled from 'styled-components'
import ProjectLine from '../components/ProjectLine'
import ListContainer from '../components/ListContainer'
import Navbar from '../components/Navbar'
import List from '../components/List'
import { titleVariants, pageVariants, sharedTransition } from '../components/shared'
import { AnimatePresence, motion } from 'framer-motion'
import { Variants } from '../types'

interface WorkDataItem {
    title: string
    stack: string[]
    website: string
    thumbnail: string
}
const ProjectsGrid = styled(motion.div)`
    width: 100%;
    display: grid;
    gap: 16px;
    grid-template-columns: repeat(auto-fit,minmax(320px, 1fr));
`

const gridVariants: Variants = {
    "grid-animate": {
        opacity: 1,
        transition: {
            staggerChildren: .1
        }
    },
    "grid-exit": {
        opacity: 0,
        transition: {
            staggerChildren: .1
        }
    }
}
const gridItemVariants: Variants = {
    "grid-animate": {
        y: 0,
        opacity: 1,
        transition: sharedTransition
    },
    "grid-exit": {
        y: '100%',
        opacity: 0,
        transition: sharedTransition

    }
}
const Work = () => {
    const [projects, setProjects] = React.useState<WorkDataItem[] | null>(null)
    const [repos, setRepos] = React.useState<any[] | null>(null)
    React.useEffect(() => {
        fetch('/projects.json')
            .then(res => res.json())
            .then(setProjects)
        fetch('https://api.github.com/users/farhat-h/repos')
            .then(res => res.json())
            .then(data => data.filter((item: any) => /typescript|javascript/i.test(item.language)))
            .then(setRepos)
    }, [])
    return (
        <Page
            animate="page-show"
            exit="page-exit"
            initial="page-exit"
            variants={pageVariants}

        >
            <Container>
                <Navbar />
                <PageIndicator variants={titleVariants}>Personal projects</PageIndicator>
                <Rule style={{ marginBottom: 32 }} />


                <AnimatePresence exitBeforeEnter>
                    {
                        projects &&
                        <ProjectsGrid
                            initial="grid-exit"
                            animate="grid-animate"
                            exit="grid-exit"
                            variants={gridVariants}
                            style={{ marginBottom: 32 }}>
                            {
                                projects.map((item, index) => <ProjectThumbnail variants={gridItemVariants} {...item} key={index} />)
                            }
                        </ProjectsGrid>
                    }

                </AnimatePresence>

                <PageIndicator>Other tinkering</PageIndicator>
                {
                    repos &&
                    <AnimatePresence>
                        <ListContainer
                            initial="grid-exit"
                            animate="grid-animate"
                            exit="grid-exit"
                            variants={gridVariants}
                            style={{ marginTop: 32 }}
                        >
                            {
                                repos.map((item, index) => <ProjectLine variants={gridItemVariants} {...item} key={`line-${index}`} />)
                            }
                        </ListContainer>
                    </AnimatePresence>
                }
            </Container>
        </Page>
    )
}
export default Work