import React from 'react'
import Page, { PageIndicator } from '../components/Page'
import Container from '../components/Container'
import Title from '../components/Title'
import ResumeEntry, { ResumeEntryProps } from '../components/ResumeEntry'
import List from '../components/List'
import Navbar from '../components/Navbar'
import { pageVariants, titleVariants } from '../components/shared'
interface ResumeData {
    workExperience: ResumeEntryProps[],
    education: ResumeEntryProps[]
}
const Resume = () => {
    const [data, setData] = React.useState<ResumeData | null>(null)

    React.useEffect(() => {
        fetch('/resume.json')
            .then(res => res.json())
            .then((data: ResumeData) => setData(data))
    }, [])
    return (
        <Page
            animate="page-show"
            exit="page-exit"
            initial="page-exit"
            variants={pageVariants}
        >
            <Container>
                <Navbar />
                <PageIndicator variants={titleVariants}>Resumé</PageIndicator>
                {
                    data &&
                    <>
                        <Title variants={titleVariants} style={{ marginBottom: 32 }}>Work Experience</Title>
                        {
                            data?.workExperience.map(
                                (item, index) => <ResumeEntry variants={titleVariants} key={index} {...item} />
                            )
                        }

                        <Title variants={titleVariants} style={{ marginBottom: 32 }}>Education and training</Title>


                        {
                            data?.education.map(
                                (item, index) => <ResumeEntry variants={titleVariants} key={index} {...item} />
                            )
                        }

                    </>
                }

            </Container>

        </Page>
    )
}

export default Resume
