import styled from 'styled-components'
import { MOBILE_QUERY } from '../helpers';
import {motion} from 'framer-motion'

interface ParagraphProps extends React.HTMLAttributes<HTMLParagraphElement> {
    responsiveCapped?: boolean
}
const Paragraph = styled(motion.p)<ParagraphProps>`
    font-size: 1.2rem;
    line-height: 150%;
    width: ${props => props.responsiveCapped ? '600px' : 'auto'};

    ${MOBILE_QUERY} {
        width: ${props => props.responsiveCapped ? '320px' : 'auto'};
    }
`
export default Paragraph;