import React from 'react'
import styled from 'styled-components'
import { motion, MotionProps, } from 'framer-motion'
import { MOBILE_QUERY } from '../helpers'
interface ProjectLineProps {
    name: string
    description: string
    language: string
    html_url: string
}
const ProjectLineWrapper = styled(motion.li) <ProjectLineProps>`
    display: flex;
    gap: 8px;
    align-items: center;
    height: 56px;
    font-size: 1.2rem;
    .description {
        flex: 1;
        color: rgba(255,255,255, .4);
        ${MOBILE_QUERY} {
            display: none
        }

    }
    .name {
        color: #fff;
        text-decoration: none;
        font-family: var(--font-titles);
        
    }
    .lang {
        margin-left: auto;
        color: rgba(255,255,255, .4);

    }
`
const ProjectLine: React.FC<ProjectLineProps & MotionProps> = (props) => {
    return (
        <ProjectLineWrapper {...props}>
            <a className="name" href={props.html_url}>
                {props.name}
            </a>
            <span className="description">
                {props.description}
            </span>

            <span className="lang">{props.language}</span>

        </ProjectLineWrapper>
    )
}

export default ProjectLine
