import styled from 'styled-components'
import { motion } from 'framer-motion'
import { MOBILE_QUERY } from '../helpers'
const Page = styled(motion.main)`
    height: 100vh;
`
interface BackgroundPageProps extends React.HTMLAttributes<HTMLDivElement> {
    image: string;
    scale?: number | string;
}
export const BackgroundPage = styled(Page) <BackgroundPageProps>`
    background: url(${props => props.image}) center no-repeat;
    background-size: 80%;
    ${MOBILE_QUERY} {
        background-size: 160%;
    }
`
export const PageIndicator = styled(motion.header)`
    display: inline-block;
    padding: 4px 10px;
    background-color: #fff;
    color: #000;
    font-family: var(--font-titles);
    font-size: 24px;
`
export default Page