import React from 'react'
import { PageIndicator, BackgroundPage } from '../components/Page'
import Title from '../components/Title'
import Container from '../components/Container'
import Paragraph from '../components/Paragraph'
import HashButton from '../components/HashButton'
import Row from '../components/Row'
import { useHistory } from 'react-router-dom'
import Navbar from '../components/Navbar'
import { pageVariants, titleVariants } from '../components/shared'

const styles: { [key: string]: React.CSSProperties } = {
    container: {
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'column',
        alignItems: 'flex-start'
    }
}
function
    Landing() {
    const history = useHistory()
    const goTo = (url: string) => history.push(url)
    return (
        <BackgroundPage
            animate="page-show"
            exit="page-exit"
            initial="page-exit"
            variants={pageVariants}
            image="/brand-logo.svg">
            <Container
                style={styles.container}
            >
                <Navbar />
                <PageIndicator variants={titleVariants}>@farhat-h</PageIndicator>
                <Title variants={titleVariants} style={{ margin: '8px 0px 32px' }} size={48} allCaps>Fullstack web developer</Title>
                <Paragraph variants={titleVariants} style={{ fontFamily: "var(--font-titles)" }} responsiveCapped>
                    Hi, my name is Haythem Farhat, I’m a
                    fullstack web developer based in Tunisia <img width={24} src="data:image/svg+xml;base64," alt="" />
                    <br />
                    I create cool apps using modern web technologies
                </Paragraph>
                <Row variants={titleVariants} style={{ marginTop: 32 }}>
                    <HashButton onClick={() => goTo('/work')} color="var(--highlight-blue)" >SEE MY WORK</HashButton>
                    <HashButton onClick={() => goTo('/resume')} color="var(--highlight-yellow)" >SEE MY RESUME</HashButton>
                    <HashButton onClick={() => goTo('/contact')} color="var(--highlight-pink)" >GET IN TOUCH</HashButton>
                </Row>
            </Container>
        </BackgroundPage>

    )
}

export default Landing
