import styled from 'styled-components'
import { motion } from 'framer-motion'
import { MOBILE_QUERY } from '../helpers'
interface TitleProps extends React.HTMLAttributes<HTMLElement> {
    size?: number
    mobileSize?: number
    allCaps?: boolean
}
const Title = styled(motion.h1) <TitleProps>`
    font-size: ${props => props.size || 48}px;
    letter-spacing: 2px;
    font-weight: 700 !important;
    text-transform: ${props => props.allCaps ? 'uppercase' : 'normal'};
    width: 600px;
    ${MOBILE_QUERY} {
        ${props => props.mobileSize || 32}px;
        max-width: 320px;
    } 
`

export default Title