import React from 'react'
import Page, { PageIndicator } from '../components/Page'
import Container from '../components/Container'
import Row from '../components/Row'
import Navbar from '../components/Navbar'
import { pageVariants, titleVariants } from '../components/shared'
import { motion } from 'framer-motion'
const styles: { [key: string]: React.CSSProperties } = {
    container: {
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'column',
        alignItems: 'flex-start'
    }
}
const Contact = () => {
    return (
        <Page
            animate="page-show"
            exit="page-exit"
            initial="page-exit"
            variants={pageVariants}
        >
            <Container style={styles.container}  >
                <Navbar />
                <PageIndicator style={{ marginBottom: 32 }}>Contact me</PageIndicator>
                <Row justifyContent="space-between" wrap>
                    <motion.a variants={titleVariants} href="mailto:farhathaythem1@gmail.com">
                        <img src="/gmail.svg" alt="gmail" />
                    </motion.a>

                    <motion.a variants={titleVariants} href="https://www.linkedin.com/in/farhat-haythem/">
                        <img src="/linkedin.svg" alt="linkedin" />
                    </motion.a>

                    <motion.a variants={titleVariants} href="https://github.com/farhat-h">
                        <img src="/github.svg" alt="github" />
                    </motion.a>

                    <motion.a variants={titleVariants} href="https://www.facebook.com/farhat.haythem.0/">
                        <img src="/facebook.svg" alt="facebook" />
                    </motion.a>
                </Row>
            </Container>
        </Page>
    )
}

export default Contact
