import React from 'react'
import { Link } from 'react-router-dom'
import Container from '../components/Container'
import { BackgroundPage } from '../components/Page'
const containerStyle: React.CSSProperties = {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    height: '100%',
}
const Lost = () => {

    return (
        <BackgroundPage image="/brand-logo.svg" >
            <Container style={containerStyle}>
                
                <h1>YOU SEEM LOST</h1>
                <Link style={{color: "#fff", fontFamily: "var(--font-titles)"}} to="/">Go back</Link>
            </Container>
        </BackgroundPage >
    )
}

export default Lost
