import React from 'react'
import Row from './Row'
import Container from './Container'
import { NavLink } from 'react-router-dom'
import styled from 'styled-components'
import { motion } from 'framer-motion'
const Link = styled(NavLink)`
    color: #fff;
`

const Navbar = () => {
    return (
        <motion.header layout layoutId="navbar">
            <Container style={{ padding: 0, height: 56, marginBottom: 32 }}>
                <Row>
                    <img src="/small-logo.svg" alt="logo" />
                    <Link to="/">
                        Home
                    </Link>
                    <Link to="/work">
                        Work
                    </Link>
                    <Link to="/resume">
                        Resume
                    </Link>

                    <Link to="/contact">
                        Contact
                    </Link>
                </Row>
            </Container>
        </motion.header>
    )
}

export default Navbar
